module.exports = "/* Harita konteynerine uygulanan stiller */\r\n.map-container {\r\n  width: 50%;\r\n  margin: auto;\r\n  border: 2px solid #333; /* Kenarlk rengi ve kalnl */\r\n  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3); /* Glge efekti */\r\n  position: relative;\r\n}\r\n/* Harita konteynernn iindeki harita divi */\r\n#map {\r\n  width: 100%;\r\n  height: 500px;\r\n  display: inline-flex;\r\n  justify-content: center; /* Yatayda ortalama */\r\n  align-items: center; /* Dikeyde ortalama */\r\n  background-position: center;\r\n}\r\n/* Sa st kedeki panel */\r\n.panel {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 10px;\r\n  margin-left: 10px;\r\n  background-color: rgba(255, 255, 255, 0.7);\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: flex-start; /* Paneli sa st kede gstermek iin \"align-items\" kullann */\r\n\r\n  gap: 10px;\r\n}\r\n.layer-container {\r\n  display: flex;\r\n  flex-direction: column; /* Bileenleri dikeyde srala */\r\n  align-items: flex-start; /* Bileenleri yatayda sola hizala */\r\n\r\n  /* stediiniz stillemeleri burada ekleyebilirsiniz */\r\n}\r\n/* Yazlarn stili */\r\n.app-layer-toggle label,\r\n.app-layer-opacity label {\r\n  /* stediiniz yaz stili */\r\n  font-weight: bold;\r\n}\r\n/* Toggle'n stili */\r\n.app-layer-toggle input[type=\"checkbox\"] {\r\n  /* stediiniz toggle stili */\r\n  margin-left: 20px; /* Yazdan 5 piksel sada grnmesini salar */\r\n}\r\n/* Opacity slider'nn stili */\r\n.app-layer-opacity input[type=\"range\"] {\r\n  /* stediiniz slider stili */\r\n  width: 100px; /* Genilik ayar, istediiniz kadar bytebilirsiniz */\r\n  margin-left: 5px; /* Toggle'dan 5 piksel sada grnmesini salar */\r\n  align-self: flex-start; /* Opacity bileenini sola hizala */\r\n}\r\n.ol-scale-bar {\r\n  background-color: rgba(255, 255, 255, 0.7);\r\n  padding: 5px 10px;\r\n  border-radius: 5px;\r\n  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);\r\n  position: absolute;\r\n  bottom: 10px;\r\n  right: 10px;\r\n}\r\n.coordinate-display {\r\n  position: absolute;\r\n  top: 6%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  background-color: rgba(79, 51, 116, 0.3); /* 0.5 opaklk (0'dan 1'e lek) */\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n  font-size: 12px;\r\n}\r\n\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"