import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class AdminGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    if (this.authService.loggedIn() && this.authService.isAdmin()) {
      // Kullanc giri yapm ve "Admin" rolne sahipse, sayfay grntlemesine izin ver
      return true;
    } else {
      // Kullanc giri yapmamsa veya "Admin" rolne sahip deilse, hata sayfasna ynlendir
      this.router.navigate(['/err']); // Hata sayfasnn yolunu uygun ekilde gncelleyin
      return false;
    }
  }
}
